"use client";

import { useEffect, useState, useCallback } from "react"; // Added useCallback
import { useRouter } from "next/navigation";
import Navbar from "../../components/Navbar";
import { getRelativeTime } from "../../lib/utils";

export default function RoleBasedDashboard() {
  const [user, setUser] = useState<any>(null);
  const [tickets, setTickets] = useState<any[]>([]);
  const router = useRouter();
  const [lastUpdated, setLastUpdated] = useState<Date>(new Date());
  const [timeAgo, setTimeAgo] = useState("just now");

  // 1. Move loadTickets outside of useEffect so it is "visible" to the component
  const loadTickets = useCallback(async () => {
    // We need to get the user from localStorage here if state isn't set yet
    const storedUser = localStorage.getItem("user");
    if (!storedUser) return;
    const parsedUser = JSON.parse(storedUser);

    try {
      const res = await fetch("http://localhost:3001/api/tickets");
      if (res.ok) {
        const allTickets = await res.json();
        const filtered = allTickets.filter((t: any) => {
          if (parsedUser.role === "Head") return t.dept === parsedUser.dept;
          return t.createdBy === parsedUser.username;
        });
        setTickets(filtered);
        setLastUpdated(new Date());
      }
    } catch (error) {
      console.error("Error loading tickets:", error);
    }
  }, []); // useCallback prevents unnecessary re-renders

  // 2. Timer effect for "Time Ago"
  useEffect(() => {
    const interval = setInterval(() => {
      const seconds = Math.floor(
        (new Date().getTime() - lastUpdated.getTime()) / 1000,
      );

      if (seconds < 60) setTimeAgo("just now");
      else if (seconds < 3600) setTimeAgo(`${Math.floor(seconds / 60)}m ago`);
      else setTimeAgo(`${Math.floor(seconds / 3600)}h ago`);
    }, 10000);

    return () => clearInterval(interval);
  }, [lastUpdated]);

  // 3. Initial Auth check and data load
  useEffect(() => {
    const storedUser = localStorage.getItem("user");

    if (!storedUser) {
      router.push("/login");
      return;
    }

    setUser(JSON.parse(storedUser));
    loadTickets(); // Now it can find the function!
  }, [router, loadTickets]);

  if (!user) {
    return (
      <div className="flex justify-center items-center h-screen bg-gray-50 text-gray-500 font-medium">
        Loading...
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <Navbar user={user} />
      <main className="max-w-7xl mx-auto py-10 px-8">
        <div className="bg-white shadow-sm rounded-xl overflow-hidden border border-gray-200">
          <div className="p-8">
            <h2 className="text-2xl font-bold text-gray-900 mb-6">
              {user.dept} {user.role} Dashboard
            </h2>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
              {/* Profile Card */}
              <div
                className={`p-6 rounded-xl border flex flex-col justify-between ${
                  user.dept === "Nursing"
                    ? "bg-red-50 border-red-100"
                    : user.dept === "IT"
                      ? "bg-blue-50 border-blue-100"
                      : "bg-green-50 border-green-100"
                }`}
              >
                <div>
                  <h3
                    className={`text-lg font-semibold uppercase tracking-wider mb-4 ${
                      user.dept === "Nursing" ? "text-red-700" : "text-blue-700"
                    }`}
                  >
                    My Profile
                  </h3>
                  <div className="space-y-2 text-sm text-gray-700">
                    <p>
                      <strong>User:</strong> {user.username}
                    </p>
                    <p>
                      <strong>Access:</strong> {user.role}
                    </p>
                    <p>
                      <strong>Dept:</strong> {user.dept}
                    </p>
                  </div>
                </div>
              </div>

              {/* Tickets Card */}
              <div className="bg-white p-6 rounded-xl border border-gray-200 shadow-sm flex flex-col justify-between">
                <div className="flex justify-between items-start">
                  <div>
                    <div className="flex items-center gap-2 mb-2">
                      <h3 className="text-gray-500 text-sm font-medium uppercase tracking-wider">
                        Active Tickets
                      </h3>
                      {/* Button now correctly calls the function */}
                      <button
                        onClick={loadTickets}
                        className="text-gray-400 hover:text-indigo-600 transition-colors"
                        title="Refresh Data"
                      >
                        <svg
                          className="w-4 h-4"
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth="2"
                            d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
                          />
                        </svg>
                      </button>
                    </div>
                    <p className="text-4xl font-bold text-gray-900">
                      {tickets.length}
                    </p>
                    <p className="text-xs text-gray-400 mt-1 italic">
                      Updated {timeAgo}
                    </p>
                  </div>
                  <div className="p-3 bg-indigo-50 rounded-lg">
                    <svg
                      className="w-6 h-6 text-indigo-600"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M15 5v2m0 4v2m0 4v2M5 5a2 2 0 00-2 2v3a2 2 0 110 4v3a2 2 0 002 2h14a2 2 0 002-2v-3a2 2 0 110-4V7a2 2 0 00-2-2H5z"
                      />
                    </svg>
                  </div>
                </div>

                <div className="mt-6 flex flex-col sm:flex-row gap-4">
                  {user.role === "User" ? (
                    <button
                      onClick={() => router.push("/tickets/create")}
                      className="flex-1 bg-purple-600 text-white font-medium py-2.5 px-4 rounded-lg hover:bg-purple-700 transition"
                    >
                      + Create New Ticket
                    </button>
                  ) : (
                    <>
                      <button
                        onClick={() => router.push("/tickets")}
                        className="flex-1 bg-indigo-600 text-white font-medium py-4 px-4 rounded-lg hover:bg-indigo-700 transition"
                      >
                        View All Tickets
                      </button>
                      {user.role === "Head" && (
                        <button
                          onClick={() => router.push("/tickets")}
                          className="flex-1 bg-white text-indigo-600 border border-indigo-200 font-medium py-4 px-4 rounded-lg hover:bg-indigo-50 transition"
                        >
                          Process {user.dept} Queue
                        </button>
                      )}
                    </>
                  )}
                </div>
              </div>
            </div>

            <div className="p-4 bg-slate-50 rounded-lg border border-dashed border-slate-300 text-center">
              <p className="text-slate-500 text-sm italic">
                Currently showing filtered results for the{" "}
                <strong>{user.dept}</strong> department.
              </p>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
